import React from "react";
import { inject, observer } from 'mobx-react/index'
import { withRouter, Switch, Redirect, Route, NavLink, Link } from 'react-router-dom'
import { Modal, Icon, Form, Input, Checkbox, message, Tree, Collapse, Dropdown, Menu } from "antd";
import api from "../../../api/api";
import { isAuthenticated } from "../../../utils/Session";

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'

const DirectoryTree = Tree.DirectoryTree;
const { TreeNode } = Tree;
const Search = Input.Search;
const Panel = Collapse.Panel;
const getSvgIcon = (style = {}, text) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    const path1 = 'M840.4 300H183.6c-19.7 0-30.7 20.8-18.5 35l328.4 380.8c9.4 10.9 27.5 10.9 37 0L858.9 335c12.2-14.2 1.2-35-18.5-35z'
    return (
        <i style={style}>
            <svg
                viewBox="0 0 1024 1024"
                width="1em"
                height="1em"
                fill="currentColor"
                style={{ verticalAlign: '-.125em' }}
            >
                <path d={path1} />
            </svg>
        </i>
    );
};
const MessageSvg = () => (
    <svg width="1em" height="1em" fill="currentColor" viewBox="0 0 1024 1024">
        <path d="M128 725.333333h170.666667v170.666667a42.624 42.624 0 0 0 72.832 30.165333L572.330667 725.333333H896a42.666667 42.666667 0 0 0 42.666667-42.666666V213.333333a42.666667 42.666667 0 0 0-42.666667-42.666666H128a42.666667 42.666667 0 0 0-42.666667 42.666666v469.333334a42.666667 42.666667 0 0 0 42.666667 42.666666zM170.666667 256h682.666666v384h-298.666666a42.496 42.496 0 0 0-30.165334 12.501333L384 793.002667V682.666667a42.666667 42.666667 0 0 0-42.666667-42.666667H170.666667V256z" fill="" p-id="1537"></path>
    </svg>
)
const MessageIcon = props => <Icon component={MessageSvg} {...props} />;


function expandIcon(props) {
    return getSvgIcon({
        position: 'absolute',
        right: 'auto',
        left: '20px',
        color: '#B4B4B4',
        transform: `rotate(${props.isActive ? 0 : -90}deg)`,
        top: 0
    }, props);
}
// @inject('articleStore') @observer
class Index extends React.Component {
    constructor(props) {
        super(props)
        console.log(this.props)
        let str = this.props.location.pathname.split('/')[this.props.location.pathname.split('/').length - 1]
        if (str.length == 1) {

            this.setState({
                id: str,

            })
        }
        if (this.props.location.pathname == '/Article/Index/HomePage') {
            this.setState({
                id: 0,

            })
        }
    }
    state = {
        listCategory: [],
        MouseLeft: false,
        MouseUp: 0,
        left: 0,
        visible: false,
        changeCheck: ['1'],
        backUserId: 0,
        secondList: [],
        id: 0,
        secondId: 0,
        status: 1,
        isCheck: 1,
        pageNo: 1,
        pages: 1,
        visibleModal: false,
        companyBuild: [],
        panelCheack: [],
        userList: [],
        countDeptEmp: [],
        keys: [],
        deptId: 1,
        checkedValues: [],
        user: [],
        deptIdCheck: '',
        isShow: false,
        articleList: []
    }
    componentWillMount() {
        // NProgress.start()
        console.log(JSON.parse(isAuthenticated().split('?')[0]))
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId
        })

        api('listCategory', 'post', {}, { 'type': 1, status: 1 }).then(res => {
            if (!res.data.errorMessage) {

                let str = this.props.location.pathname.split('/')[this.props.location.pathname.split('/').length - 1]
                console.log(str)

                if (str.length <= 3) {
                    for (let index = 0; index < res.data.data.length; index++) {
                        const element = res.data.data[index];
                        if (element.id == str) {
                            this.setState({
                                secondList: element.list,

                            })
                        }
                    }
                    if (str == 0) {
                        this.setState({
                            secondList: res.data.data[0].list,

                        })
                    }
                    console.log(str)
                    this.setState({
                        id: str == 0 ? res.data.data[0].id : str,
                        listCategory: res.data.data,
                    })
                } else {
                    str = res.data.data[0].id
                    this.setState({
                        listCategory: res.data.data,
                        secondList: res.data.data[0].list,
                        id: res.data.data[0].id,

                    })
                }
                this.getList(userInfo.backUserId, str == 0 ? res.data.data[0].id : str).then(res => {
                    console.log(res)
                    if (!res.data.errorMessage) {
                        this.setState({
                            articleList: res.data.list,
                            total: res.data.total,
                            pages: res.data.pages,
                            user: res.data.users
                        })
                    } else {
                        message.warning(res.data.errorMessage, 2)
                        this.setState({
                            isShow: true
                        })
                    }
                })
            } else {
                message.warning(res.data.errorMessage, 2)
                this.setState({
                    isShow: true
                })
            }
        })
        api('countDeptEmp', 'post').then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    countDeptEmp: res.data.data
                })
            }
        })
        api('getCompanyBuild', 'post').then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    companyBuild: res.data.data,
                    keys: [res.data.data.id.toString()]
                })
                api('getBackUserByDeptId', 'post', {
                    "deptId": res.data.data.id,
                    "pageNo": 1,
                    "pageSize": 10000
                }).then(res => {
                    if (!res.data.errorMessage) {
                        this.setState({
                            userList: res.data.list
                        })
                    }
                })
            }
        })
    }

    componentDidMount() {
        NProgress.done()
        console.log(this.state.secondList)
    }
    componentWillUnmount() {
        NProgress.start()
    }
    getList = async (adminId, firstCateId, categoryId = '', status = this.state.status, pageNo = 1, pageSize = 10) => {
        let res = await api('getArticleListByCate', 'post', {}, {
            'firstCateId': firstCateId,
            'adminId': adminId,
            'categoryId': categoryId,
            'pageNo': pageNo,
            'status': status,
            'pageSize': pageSize
        })

        return res
    }
    onClick = (val) => {
        console.log(val)
        this.setState({
            id: val.id,
            secCateId: val.list[0] ? val.list[0].id : '',
            secondList: val.list
        })
        this.getList(this.state.backUserId, val.id).then(res => {
            console.log(res)
            if (!res.data.errorMessage) {
                this.setState({
                    articleList: res.data.list,
                    total: res.data.total,
                    user: res.data.users,
                    pageNo: res.data.pages
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    onMouseDown = (e) => {
        e.preventDefault()
        this.setState({
            MouseLeft: true
        })

    }
    onMouseUp = (e) => {
        e.preventDefault()
        this.setState({
            MouseLeft: false
        })

    }
    onMouseLeave = (e) => {
        e.preventDefault();
        this.setState({
            MouseLeft: false
        })

    }
    onMouseMove = (e) => {
        e.preventDefault();
        let width = document.getElementsByClassName('columName')[0].clientWidth - document.getElementsByClassName('head')[0].clientWidth
        if (this.state.MouseLeft && width > 0) {
            if (this.state.left >= 0 && e.movementX > 0) {
                console.log(1);

                return
            }
            if (Math.abs(this.state.left) >= width && e.movementX < 0) {
                console.log(2);

                return


            } else {
                if (Math.abs(this.state.left + e.movementX) >= width) {
                    console.log(3);

                    this.setState({
                        left: 0 - width
                    })
                } else {
                    console.log(5);

                    this.setState({
                        left: this.state.left + e.movementX
                    })
                }

            }

        }
    }
    getSecond = (val) => {
        console.log(val)
        if (val == 0) {
            this.setState({
                secondId: 0
            })
        } else {
            this.setState({
                secondId: val
            })
        }

        this.getList(this.state.backUserId, val == 0 ? this.state.id : '', val == 0 ? '' : val).then(res => {
            console.log(res)
            if (!res.data.errorMessage) {
                this.setState({
                    articleList: res.data.list,
                    pages: res.data.pages,
                    pageNo: 1,
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    setNavLink = (val, index) => {
        if (index == 0) {
            return (
                <div key={val.id} className={this.state.id == val.id || this.state.id == 0 ? 'link-active' : ''} onClick={() => this.onClick(val, index)} >{val.name}</div>
            )
        } else {
            return (
                <div key={val.id} className={this.state.id == val.id ? 'link-active' : ''} onClick={() => this.onClick(val, index)} >{val.name}</div>
            )
        }

    }
    setSecondlist = (val, index) => {
        return (
            <div className={this.state.secondId == val.id ? 'secondList second-active' : 'secondList'} onClick={() => this.getSecond(val.id, index)} >{val.name}</div>
        )


    }
    showModal = () => {
        // this.setState({
        //     visible: true
        // })
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };
    changeCheckNo = (val) => {
        console.log(val)
        if (val.target.checked) {
            this.props.form.setFieldsValue({ 'checkboxGroup': ['0'] })
            // this.setState({
            //     changeCheck:['0']
            // })
        } else {
            this.props.form.setFieldsValue({ 'checkboxGroup': ['1'] })
            // this.setState({
            //     changeCheck:['1']
            // })
        }
    }
    changeCheckYes = (val) => {
        console.log(val)
        if (val.target.checked) {
            this.props.form.setFieldsValue({ 'checkboxGroupNo': ['1'] })

            // this.setState({
            //     changeCheck:['1']
            // })
        } else {
            this.props.form.setFieldsValue({ 'checkboxGroupNo': ['0'] })
            // this.setState({
            //     changeCheck:['0']
            // })
        }
    }
    cancelTop = (val) => {
        console.log(val)
        api('setTop', 'post', {}, {
            isTop: 2,
            backUserId: this.state.backUserId,
            articleId: this.state.articleList[val].id
        }).then(res => {
            if (!res.data.errorMessage) {
                this.state.articleList[val].isTop = 2
                message.success('操作成功')

                this.setState({
                    articleList: this.state.articleList
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    isTop = (val) => {
        api('setTop', 'post', {}, {
            isTop: 1,
            backUserId: this.state.backUserId,
            articleId: this.state.articleList[val].id
        }).then(res => {
            if (!res.data.errorMessage) {
                this.state.articleList[val].isTop = 1
                message.success('操作成功')
                this.setState({
                    articleList: this.state.articleList
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    setStatus = (val) => {
        console.log(val)
        NProgress.start()
        this.getList(this.state.backUserId, this.state.secondId == 0 ? this.state.id : '', this.state.secondId == 0 ? '' : this.state.secondId, val).then(res => {
            console.log(res)
            NProgress.done()

            if (!res.data.errorMessage) {
                this.setState({
                    articleList: res.data.list,
                    total: res.data.total,
                    status: val,
                    pages: res.data.pages,
                    pageNo: 1,

                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }
        }).catch(() => {
        })
    }
    setCheck = (val) => {
        this.setState({
            isCheck: val
        })
    }

    operate = () => {
        this.props.history.push('/Edit')
    }
    setPrev = () => {
        if (this.state.pageNo == 1) {
            return
        } else {

            NProgress.start()
            this.getList(this.state.backUserId, this.state.secondId == 0 ? this.state.id : '', this.state.secondId == 0 ? '' : this.state.secondId, this.state.status, this.state.pageNo - 1).then(res => {
                console.log(res)
                NProgress.done()
                this.setState({
                    pageNo: this.state.pageNo - 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        articleList: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages,
                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            }).catch(() => {
            })
        }
    }
    setNext = () => {
        if (this.state.pageNo == this.state.pages) {
            return
        } else {

            NProgress.start()
            this.getList(this.state.backUserId, this.state.secondId == 0 ? this.state.id : '', this.state.secondId == 0 ? '' : this.state.secondId, this.state.status, this.state.pageNo + 1).then(res => {
                console.log(res)
                NProgress.done()
                this.setState({
                    pageNo: this.state.pageNo + 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        articleList: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages,
                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            }).catch(() => {
            })
        }
    }
    visibleModalOk = () => {

        this.setState({
            visibleModal: true
        })
        api('getBackUserByDeptId', 'post', {
            "deptId": this.state.companyBuild.id,
            "pageNo": 1,
            "pageSize": 10000
        }).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    userList: res.data.list
                })
            }
        })
    }
    visibleModalCancel = () => {
        this.setState({
            visibleModal: false
        })
    }
    getCount = (val) => {
        if (val) {
            let str = ''
            this.state.countDeptEmp && this.state.countDeptEmp.map(res => {

                if (res.deptId == val) {
                    str = res.count
                }
            })
            return str
        }
    }
    onSelect = (keys, event) => {
        console.log('Trigger Select', keys, event);
        api('getBackUserByDeptId', 'post', {
            "deptId": keys[0],
            "pageNo": 1,
            "pageSize": 10000
        }).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    userList: res.data.list
                })
            }
        })
    };


    onChange = (checkedValues) => {
        console.log('checked = ', checkedValues);
        this.setState({
            checkedValues
        })
    }
    sendAdjust = () => {
        api('saveCategoryEdit', 'post', {}, {
            'adminId': this.state.backUserId,
            'backUserId': [...new Set(this.state.checkedValues)].join(''),
            'categoryId': this.state.secondId = 0 ? this.state.secondId : this.state.id
        }).then(res => {
            if (!res.data.errorMessage) {
                this.visibleModalCancel()

                this.getList(this.state.backUserId, this.state.id, '').then(res => {
                    console.log(res)
                    if (!res.data.errorMessage) {
                        this.setState({
                            user: res.data.users
                        })
                    } else {
                        message.warning(res.data.errorMessage, 2)
                    }
                })
            } else {
                this.visibleModalCancel()
                message.warning(res.data.errorMessage, 2)
            }
        })
    }
    onCollapseChange = (key) => {
        console.log(key)

        if (key) {
            api('getBackUserByDeptId', 'post', {
                "deptId": key,
                "pageNo": 1,
                "pageSize": 10000
            }).then(res => {
                if (!res.data.errorMessage) {
                    this.setState({
                        userList: res.data.list
                    })
                }
            })
        }
    }
    onChangeSecond = (val) => {
        console.log(val)
        this.setState({
            deptIdCheck: val
        })
        api('getBackUserByDeptId', 'post', {
            "deptId": val,
            "pageNo": 1,
            "pageSize": 10000
        }).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    userList: res.data.list
                })
            }
        })
    }
    setShelf = (val, id) => {
        let str = val == 1 ? 2 : 1
        NProgress.start()
        api('onShelfArticle', 'post', {}, { 'adminId': this.state.backUserId, 'status': str, 'articleId': id }).then(res => {
            NProgress.done()

            if (!res.data.errorMessage) {
                message.success('操作成功！')

                this.getList(this.state.backUserId, this.state.secondId == 0 ? this.state.id : '', this.state.secondId == 0 ? '' : this.state.secondId, this.state.status).then(res => {
                    console.log(res)

                    if (!res.data.errorMessage) {
                        this.setState({
                            articleList: res.data.list,
                            total: res.data.total,
                            pages: res.data.pages,
                            pageNo: 1,

                        })
                    } else {
                        message.warning(res.data.errorMessage, 2)
                    }
                })
            } else {
                message.warning(res.data.errorMessage, 2)
            }

        })
    }
    setMenu = (val, id) => {
        if (val == 1) {
            return (
                <Menu>
                    <Menu.Item>
                        <Link to={{ 'pathname': '/Article/Edit', params: { 'id': id } }}>
                            编辑
                    </Link>

                    </Menu.Item>


                    <Menu.Item>
                        <a target="_blank" rel="noopener noreferrer" href="javascript:;" onClick={() => { this.setShelf(1, id) }}>
                            下架文章
                    </a>
                    </Menu.Item>

                </Menu>
            )
        } else {
            return (
                <Menu>
                    <Menu.Item>
                        <Link to={{ 'pathname': '/Article/Edit', params: { 'id': id } }}>
                            编辑
                </Link>
                    </Menu.Item>
                    <Menu.Item>
                        <a target="_blank" rel="noopener noreferrer" href="javascript:;" onClick={() => { this.setShelf(2, id) }}>
                            上架文章
                    </a>
                    </Menu.Item>

                </Menu>
            )
        }

    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <div id="homePage">
                <div className={this.state.isShow ? 'hide' : 'headBox'} >
                    <div className="head" onMouseDown={this.onMouseDown} onMouseLeave={this.onMouseLeave} onMouseUp={this.onMouseUp} onMouseMove={this.onMouseMove}>
                        <div className="columName" style={{ left: this.state.left + 'px' }}>
                            {
                                this.state.listCategory.map((val, index) => {
                                    return this.setNavLink(val, index)
                                })
                            }
                        </div>

                    </div>
                    <Link className="edit" to={{ pathname: '/Article/Index/Column', params: { 'visible': true } }}>
                        <Icon type="plus" style={{ 'fontSize': '24px', 'fontWeight': 600, 'color': '#999' }} />
                    </Link>

                </div>
                <div className={this.state.isShow ? 'hide' : 'content'} >
                    {
                        this.state.secondList.length > 0 ? <div className="secondType" >
                            <div className={this.state.secondId == 0 ? 'secondList second-active' : 'secondList'} onClick={() => this.getSecond(0)}>全部</div>
                            {
                                this.state.secondList.map((val, index) => {
                                    return this.setSecondlist(val, index)
                                })
                            }
                        </div> : ''
                    }
                    <div className='columnEdit'>
                        <img src={require('../../../assets/images/icon-article-column.png')} alt="" />栏目编辑
                   </div>
                    <div className="columnEditBox">
                        <span onClick={this.visibleModalOk}>栏目编辑</span>
                    </div>
                    <div className='columnEditList'>
                        {
                            this.state.user && this.state.user.map(res => {
                                return (
                                    <div className="list">
                                        <img src={res.headImg} alt="" />
                                        <span className='name'>{res.name}</span>
                                        <span className='dept'>{res.deptName}</span>


                                    </div>
                                )
                            })
                        }
                        {
                            this.state.user.length == 0 ? <div className='noData'>
                                <img src={require('../../../assets/images/img-nodata.png')} alt="" />
                                <p>暂无相关信息</p>
                            </div> : ''
                        }
                    </div>
                    <div className='articleEdit'>
                        <img src={require('../../../assets/images/icon-article-1.png')} alt="" />文章编辑
                   </div>
                    <div className="articleEditBox">
                        <span className={this.state.status == 1 ? 'status-active' : ''} onClick={() => this.setStatus(1)}>上架中</span>
                        <span className={this.state.status == 2 ? 'status-active' : ''} onClick={() => this.setStatus(2)}>下架中</span>
                    </div>
                    <div className="articleList">
                        {
                            this.state.articleList && this.state.articleList.map((res, index) => {
                                return (
                                    <div className="list" key={res.id}>
                                        <div className="title">{res.title}</div>
                                        <div className="flexBox list_foot">
                                            <div className="look">
                                                <Icon type="eye" style={{ color: '#999999', fontSize: '16px' }} />
                                                浏览 {res.browseTimes ? res.browseTimes : 0}
                                            </div>
                                            <div className="star">
                                                <Icon type="like" style={{ color: '#999999', fontSize: '16px' }} />
                                                点赞 {res.starCount ? res.starCount : 0}
                                            </div>
                                            <div className="comment">
                                                <MessageIcon style={{ color: '#999999', fontSize: '16px' }} />
                                                评论 {res.replyCount ? res.replyCount : 0}
                                            </div>
                                        </div>
                                        {
                                            res.isTop == 1 ? <img src={require('../../../assets/images/img-article-top.png')} alt="" /> : ''
                                        }
                                        {
                                            res.isTop == 1 ? <div className='cancelTop' onClick={() => this.cancelTop(index)}>取消置顶</div> : <div className='isTop' onClick={() => this.isTop(index)}>置顶</div>
                                        }
                                        <Dropdown overlay={this.setMenu(res.status, res.id)} placement="bottomCenter">
                                            <div className="operation">
                                                操作
                                     </div>
                                        </Dropdown>
                                    </div>
                                )
                            })
                        }
                        {
                            this.state.articleList.length == 0 ? <div className='noData'>
                                <img src={require('../../../assets/images/img-nodata.png')} alt="" />
                                <p>暂无相关文章</p>
                            </div> : ''
                        }
                        {
                            this.state.pages <= 1 ? '' : <div className='pagination'>
                                <span onClick={this.setPrev}>
                                    <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                                </span>
                                <span>
                                    {this.state.pageNo}/{this.state.pages}
                                </span>
                                <span onClick={this.setNext}>
                                    <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                                </span>
                            </div>
                        }
                    </div>

                </div>

                <Modal
                    title="栏目编辑"
                    visible={this.state.visibleModal}
                    onOk={this.visibleModalOk}
                    onCancel={this.visibleModalCancel}
                    footer={null}
                    closable={false}
                    centered={true}
                    width='51.56%'
                    destroyOnClose={true}
                    wrapClassName='adjustColumn'
                >
                    <div className="container">
                        <div className="flex select">
                            <div className="title">选择：</div>
                            <div className="content">
                                <div className="search">
                                    <Search
                                        placeholder="搜索"
                                        onSearch={this.getSearch}
                                        style={{ width: '91.63%' }}
                                    />
                                </div>
                                <div className="deptList">
                                    <div className="firstDrpt">
                                        <div className="head" onClick={() => { this.onChangeSecond(this.state.companyBuild.id) }}>
                                            <Icon type="caret-down" style={{ 'color': '#B4B4B4' }} /><img src={require('../../../assets/images/icon-article-2.png')} alt="" /> <span>{this.state.companyBuild.name}</span>
                                        </div>
                                        <Collapse accordion expandIcon={expandIcon} bordered={false} showArrow={false} onChange={this.onCollapseChange}>
                                            {
                                                this.state.companyBuild.list && this.state.companyBuild.list.map((res, index) => {
                                                    return (
                                                        <Panel header={<span onClick={() => { this.onChangeSecond(res.id) }}>
                                                            <img src={require('../../../assets/images/img-staff-2.png')} alt="" />
                                                            <span>{res.name}</span>
                                                        </span>
                                                        } key={res.id} >
                                                            <div >
                                                                {res.list && res.list.map((res, i) => {
                                                                    if (res && res.name) {
                                                                        return (<span style={this.state.deptIdCheck == res.id ? { 'background': '#F1948E', 'color': '#fff' } : { 'background': '#f4f6f8' }} className='second' onClick={() => {
                                                                            this.onChangeSecond(res.id)
                                                                        }} key={res.id}>{res.name}</span>)
                                                                    }


                                                                })}
                                                            </div>
                                                        </Panel>
                                                    )
                                                })
                                            }

                                        </Collapse>
                                    </div>
                                </div>
                                {/* {
                                    this.setDirectoryTree()
                                } */}
                            </div>
                        </div>
                        <div className="flex selected">
                            <div className="title">已选：</div>
                            <div className="content">
                                <Checkbox.Group style={{ width: '100%' }} onChange={this.onChange} >
                                    {
                                        this.state.userList && this.state.userList.map(res => {
                                            let str = 2
                                            this.state.user.map(val => {
                                                if (val.id == res.id) {
                                                    str = 1
                                                }
                                            })
                                            if (str == 2) {
                                                return (
                                                    <div key={res.id}>
                                                        <Checkbox value={res.id}>{res.name}</Checkbox>
                                                    </div>
                                                )
                                            }

                                        })
                                    }


                                </Checkbox.Group>
                            </div>

                        </div>

                    </div>
                    <div className="btn">
                        <div className="send" onClick={this.sendAdjust}>确认</div>
                        <div className="close" onClick={this.visibleModalCancel}>取消</div>
                    </div>
                </Modal>

            </div>
        )
    }

}
const Styles = {
    hide: {
        'display': 'none'
    },
    bg: {
        'background': '#fff'
    }
}
const WrappedApp = Form.create({ name: 'coordinated' })(Index);
export default WrappedApp
